define(['locale', 'lodash', 'docs.utilities'], function(locale, _, utility) {
    "use strict";

    function getMicrosoftPrivacyStatementLink(locale) {
        return "//privacy.microsoft.com/" + _.escape(locale) + '/';
    }

    function getHeaderWrapper() {
        return $("header");
    }

    function getFooterWrapper() {
        return $("#footerWrapper");
    }

    var path = utility.getUrlRelativeToThemeBase('templates/uhf.xml');

    $.ajax({
        url: path,
        dataType: "xml",
        global: false
    }).done(function(uhfXml) {
        var uhf = $(uhfXml);

        var footer = getFooterWrapper();
        footer.replaceWith(uhf.find('footerHtml').text());

        var headerLocString = JSON.parse(uhf.find('headerLocJson').text());
        var header = getHeaderWrapper();

        header.find('#microsoft').text(headerLocString.microsoft);
        header.find('#close').attr('title', headerLocString.close);
        header.find('#subheader\\.aboutdocs').text(headerLocString['subheader.aboutdocs']);
        
        header.find('#privacystatement-link').text(headerLocString.learnmore);
        header.find('#cookie\\.consent\\.europe').text(headerLocString['cookie.consent.europe']);

        $('a.logo img').attr('src', utility.getUrlRelativeToThemeBase('images/logo-ms.png'));

        var currentLocale = locale.getLanguageFromUrl();
        if (currentLocale) {
            currentLocale = currentLocale.toLowerCase();
            $('#footer-privacy-link').attr('href', getMicrosoftPrivacyStatementLink(currentLocale));
        }

        var impressum = require('impressum');
    });
});


