/**
 *
 * DOCS Website Layers:
 *     1. Browser Abstraction Layer:  NONE
 *        [Unify the very basic JS prototypes for cross-browser compatibilities]
 *     2. Localizable Layer: docs.stringresources
 *        [Provide an abstraction of localizable stuffs]
 *     3. Underlying Driver Layer:    docs.utilities, docs.metadata, docs.mainframe
 *        [Provide very fundamental utilities and page DOM manipulation toolkits]
 *     4. Component Rendering Layer:  docs.breadcrumb, docs.toc, docs.azureselector
 *        [Renders part of the page (reusable components) according to the business logic]
 *     5. Application Layer:          conceptual
 *        [BLAH BLAH BLAH]
 *
 * ... { Not decided yet, feel free to modify it: junyi@microsoft.com }
 *
 */


define(function () {
    "use strict";

    // setup trace and global error handler at the very beginning
    var trace = require('trace');
    // TODO: figure out a solution so that both consoleTraceListener and insight can receive all traces.
    // Temporarily disable consoleTraceListener, otherwise performance events will be consumed by it and won't be received by insight
    // trace.subscribe(trace.consoleTraceListener);
    var errors = require('errors');

    var cookies = require('cookies');

    var locale = require('locale');

    var headerfooter = require('headerfooter');

    window.NinjaMode = (cookies.getCookie('NinjaMode') === 'true');

    String.prototype.format = String.prototype.f = function () {
        var s = this,
            i = arguments.length;

        while (i--) {
            s = s.replace(new RegExp('\\{' + i + '\\}', 'gm'), arguments[i]);
        }
        return s;
    };

    var layoutConstants = {
        mobileWidthInPixel: 600,
        tabletWidthInPixel: 769,
        desktopWidthInPixel: 980,
        wideScreenWidthInPixel: 1180
    };
    var docs = {
        // build navigation
        buildNav: function (type) {
            var firstNav, secondNav;
            if (type === 'mobilenavi') {
                firstNav = '<option value="{0}">{1}</option>';
                secondNav = '<option value="{0}">&nbsp;&nbsp;{1}</option>';
            }
            else if (type === 'navi') {
                firstNav = '<h2><a href="#{0}">{1}</a></h2>';
                secondNav = '<li><a href="#{0}" title="">{1}</a></li>';
            }
            var navigation = firstNav.format('overview', 'Overview');
            var overviewFlag = false;
            if ($('header#summary').length) {
                overviewFlag = true;
                navigation += '<ul>' + secondNav.format('summary', 'Introduction');
            }
            if ($('header#conceptual').length) {
                overviewFlag = true;
                navigation += secondNav.format('conceptual', 'Using');
            }
            if ($('header#remarks').length) {
                overviewFlag = true;
                navigation += secondNav.format('remarks', 'Remarks');
            }
            if (overviewFlag) {
                navigation += '</ul>';
            }
            else {
                navigation = '';
            }
            // class block
            if ($('header#constructors').length) {
                navigation = this.buildCodeNav(navigation, 'constructors', firstNav, secondNav);
            }
            if ($('header#fields').length) {
                navigation = this.buildCodeNav(navigation, 'fields', firstNav, secondNav);
            }
            if ($('header#properties').length) {
                navigation = this.buildCodeNav(navigation, 'properties', firstNav, secondNav);
            }
            if ($('header#methods').length) {
                navigation = this.buildCodeNav(navigation, 'methods', firstNav, secondNav);
            }
            if ($('header#events').length) {
                navigation = this.buildCodeNav(navigation, 'events', firstNav, secondNav);
            }
            if ($('header#operators').length) {
                navigation = this.buildCodeNav(navigation, 'operators', firstNav, secondNav);
            }
            // namespace block
            if ($('header#classes').length) {
                navigation = this.buildCodeNav(navigation, 'classes', firstNav, secondNav);
            }
            if ($('header#interfaces').length) {
                navigation = this.buildCodeNav(navigation, 'interfaces', firstNav, secondNav);
            }
            if ($('header#structs').length) {
                navigation = this.buildCodeNav(navigation, 'structs', firstNav, secondNav);
            }
            if ($('header#enums').length) {
                navigation = this.buildCodeNav(navigation, 'enums', firstNav, secondNav);
            }
            if ($('header#delegates').length) {
                navigation = this.buildCodeNav(navigation, 'delegates', firstNav, secondNav);
            }
            return navigation;
        },

        buildCodeNav: function (navigation, header, firstNav, secondNav) {
            var parentNav = firstNav.format(header, header) + '<ul>';
            var childNav = '';
            $('header#' + header).next().children().filter(':visible').each(function () {
                var id = $(this).attr('id');
                var name = $(this).find('h4 code:visible').text();
                childNav += secondNav.format(id, name);
            });
            if (childNav.length) {
                $('header#' + header).attr('hidden', false);
                navigation += parentNav + childNav + '</ul>';
            }
            else {
                $('header#' + header).attr('hidden', true);
            }
            return navigation;
        },

        positionSidebar: function() {
            if (!!$('#sidebar').offset()) { // make sure "#sidebar" element exists
                var sidebarTop = $('#sidebar').offset().top;

                var positionSidebarImpl = function() {
                    if (layoutConstants.tabletWidthInPixel > window.innerWidth) {
                        $('#sidebar').css('display', 'none');
                        return true;
                    }
                    $('#sidebar').css('display', 'block');
                    var windowTop = $(window).scrollTop();
                    var elemTop = $('#main').offset().top + $('#main').height();
                    $('.footer-layout').each(function() {
                        elemTop = Math.min($(this).offset().top, elemTop);
                    });

                    var maxHeight = Math.min($(window).height(), elemTop - windowTop);
                    if (sidebarTop < windowTop) {
                        $('#sidebar').css({
                            position: 'fixed',
                            top: 0,
                            'height': maxHeight
                        });
                    } else {
                        $('#sidebar').css('position', 'static');
                        $('#sidebar').css('height', maxHeight - sidebarTop + windowTop);
                    }
                };
                // Initially position sidebar:
                positionSidebarImpl();
                // Position sidebar with scroll event:
                $(window).scroll(positionSidebarImpl);
                $(window).resize(positionSidebarImpl);
            }
        }
    };

    var perf = require('perf');
    var perfEvents = require('perf.events');

    $(function () {
        // P1 Contents loaded. For any page, when this function is triggered,
        // DOM should be ready and user should see the text contents with correct style.
        perf.triggerEvent(perfEvents.PERF_EVENT_P1_CONTENT_LOADED);
    });

    return docs;
});






