$(function () {
    "use strict";

    var docs = require("docs");
    var docsToc = require("docs.toc");
    var docsBread = require("docs.breadcrumb");
    var docsPersonality = require("docs.personality");
    var q = require('q');
    var util = require('util');
    var perf = require('perf');
    var perfEvents = require('perf.events');
    var locale = require('locale');
    
    var uiLocale = locale.getLanguageFromUrl(); // user preferred locale specified in url
    // Comment button disabled for all locales except en-us
    if (!uiLocale || uiLocale.toLowerCase() !== 'en-us') {
       $('#comments-link').hide();
    }

    // Render async component:
    q.allSettled([q(docsToc.renderConceptualTocAsync()), q(docsBread.renderConceptualBreadcrumbAsync())]).then(function () {
        // These are second-tier components and will start loading after toc/breadcrumb rendered.
        require(["docs.azureselector", "docs.branchselector", "commenting", "experiment", "insight", "rating", "survey"],
            function (docsAzureSel, docsBranchSel) {

                // Azure Selectors
                docsAzureSel.renderAzureSelectors();

                // Branch Selector
                docsBranchSel.renderBranchSelectorAsync();

                require(["docs.config"], function (config) {
                    util.loadLibrary(config.paths.openfeedback);
                });

                // Code blocks
                require(["highlightjs"], function (hljs) {
                    $('pre code').each(function (i, block) {
                        hljs.highlightBlock(block);
                    });
                });
            });

        perf.triggerEvent(perfEvents.PERF_EVENT_P2_CONTENT_LOADED);
    });

    // In topic TOC
    docsToc.renderInTopicToc();

    // #menu-bar toggling
    $("#menu-expand").click(function () {
        $(".social-actions").slideToggle("fast", function () {
            if ($(".social-actions").css("display") === "none") {
                $("#menu-expand-icon").text("+ ");
            } else {
                $("#menu-expand-icon").text("- ");
            }
            // post-animation actions
        });
    });

    // theme switching
    docsPersonality.hookThemeSetting();

    // mobile navi switching
    $('.mobilenavi').change(function () {
        var target = $('.mobilenavi select').val();
        $(location).attr('href', target);
    });

    // build share url
    var shareLink = location.origin + location.pathname;
    var facebookShareUrl = util.buildFacebookShareUrl(shareLink);
    var twitterShareUrl = util.buildTwitterShareUrl(shareLink, document.title);
    $(".share-facebook").attr("href", facebookShareUrl);
    $(".share-twitter").attr("href", twitterShareUrl);

    // share toggling
    $('body').not('.share-container').click(function () {
        $('.share-container').hide();
    });
    $('.sharebutton').click(function () {
        var pos = $('.sharebutton').position();
        $('.share-container').css('left', pos.left);
        $('.share-container').css('top', pos.top + 36);
        $('.share-container').toggle();
        return false;
    });

    // social share window control
    $('.share-container > li > a').click(function () {
        window.open(this.href, '', 'width=600,height=650');
        return false;
    });

    // add alert effect to TIP/NOTE/IMPORTANT/WARNING
    $('.TIP, .NOTE, .IMPORTANT, .WARNING').addClass('alert');
});


