(function () {
    "use strict";
    var locale = require('locale');
    var cookies = require('cookies');
    var _ = require('lodash');
    var europeLocaleList = require('docs.config').europeLocaleList;
    var impressumLinks = require('docs.config').impressumLinks;

    var getMicrosoftPrivacyStatementLink = function(locale) {
        return "//privacy.microsoft.com/" + _.escape(locale) + '/privacystatement/';
    };

    var isEuropeLocale = function(currentLocale) {
        return currentLocale && _.indexOf(europeLocaleList, currentLocale.toLowerCase()) > -1;
    };

    var currentLocale = locale.getLanguageFromUrl();
    if (currentLocale) {
        currentLocale = currentLocale.toLowerCase();
        if (impressumLinks[currentLocale]) {
            // show impressum link.
            $('#impressum-link').attr('href', impressumLinks[currentLocale]);
            $('#impressum-section').show();
            $('#impressum-section').css('display', 'inline');
        }
        if (isEuropeLocale(currentLocale)) {
            $('#privacystatement-link').attr('href', getMicrosoftPrivacyStatementLink(currentLocale));
            $('#cookie-consent-section').css('display', cookies.getCookie('hideconsent') ? 'none' : 'inline-block');
            $('#cookie-consent-section div.right a').click(function() {
                cookies.setCookie('hideconsent', 1);
                $('#cookie-consent-section').css('display', 'none');
            });
        }
    }
})();


